import {
  CCard,
  CCardBody,
  CCardHeader,
  CCardFooter,
  CNav,
  CNavItem,
  CNavLink,
  CTabContent,
  CTabPane,
  CTabs,
} from "@coreui/react";
import { useEffect, useState } from "react";
import { cyberTiplineGeneratePdf, showCyberTipline } from "../../api";
import qs from "qs";
import { dateIndo } from "../../utils/formatter";
import fileDownload from "js-file-download";
import LoadingButton from "../../components/LoadingButton";
import TabDetail from "./components/detail/TabDetail";
import TabFile from "./components/detail/TabFile";
import { isRole } from "../../utils/auth";
import Role from "../../constants/roles";
import TabReport from "./components/detail/TabReport";

const CyberTiplineDetail = (props) => {
  const queryString = qs.parse(props.location.search, { ignoreQueryPrefix: true });
  const id = queryString.id;
  const [data, setData] = useState([[], []]);
  const [ctNumber, setCtNumber] = useState(null);
  const [loadingPdf, setLoadingPdf] = useState(false);
  const [files, setFiles] = useState([]);
  const [report, setReport] = useState(null);
  const [loadingApi, setLoadingApi] = useState(true);
  const [tabs, setTabs] = useState([
    { name: "Detail", component: TabDetail },
    { name: "Files", component: TabFile },
  ]);
  const [activeTab, setActiveTab] = useState(tabs[0]?.name);

  const statuss = ["Batal", "Sidik", "Lidik", "Selesai"];

  const renderIpAddress = (ips) => {
    return JSON.parse(ips).map((ip) => (
      <span className="badge bg-secondary mr-1" key={ip}>
        {ip}
      </span>
    ));
  };

  const parseEmail = (emails) => {
    return (
      <div className="w-50">
        {emails.length === 0
          ? "-"
          : emails.map((e) => (
              <span key={e.id}>
                <span className="badge bg-light mr-1">{e.email}</span>
                <br />
              </span>
            ))}
      </div>
    );
  };

  const loadData = async () => {
    setLoadingApi(true);
    try {
      const { data } = await showCyberTipline(id);
      const r = data;
      setCtNumber(r.ctNumber);
      setFiles(r.fileInfos);
      setReport(r.report);
      const fields = [
        [
          { label: "CT Number", value: r.ctNumber },
          { label: "Priority", value: r.priority },
          { label: "Reporting ESP", value: r.reportingEsp },
          { label: "Suspect Email", value: parseEmail(r.suspectEmails) },
          { label: "Suspect Phone", value: r.suspectPhone },
          { label: "Suspect Screen Name", value: r.suspectScreenName },
          { label: "Suspect Date of Birth", value: dateIndo(r.suspectDob) },
          { label: "Date Made Available", value: dateIndo(r.dateMadeAvailable) },
          { label: "Incident Date", value: dateIndo(r.incidentDate) },
        ],
        [
          { label: "Status", value: <span className="badge badge-secondary">{statuss[r?.status ?? 1]}</span> },
          { label: "Created At", value: dateIndo(r.createdAt) },
          { label: "Created By", value: r.createdBy?.name },
          { label: "Analyzed By", value: r.analyzedBy?.name ?? "-" },
          { label: "Suspect IP Address", value: renderIpAddress(r.suspectIpAddress) },
          { label: "Case Information", value: r.caseInformation },
          { label: "Notes", value: r.notes },
        ],
      ];
      setData(fields);
    } catch (err) {}
    setLoadingApi(false);
  };

  const downloadPdf = async () => {
    setLoadingPdf(true);
    try {
      const res = await cyberTiplineGeneratePdf(id);
      fileDownload(res.data, `${ctNumber}.pdf`);
    } catch (err) {}
    setLoadingPdf(false);
  };

  useEffect(() => {
    if (isRole(Role.ANALYST)) {
      setTabs([...tabs, { name: "Report", component: TabReport }]);
    }
    loadData();
  }, []);
  return (
    <CCard>
      <CCardHeader>
        <h5 className="d-inline">Detail Cyber Tipline</h5>
      </CCardHeader>
      <CCardBody style={{ minHeight: 600 }}>
        <CTabs activeTab={activeTab}>
          <CNav variant="tabs">
            {tabs.map((t) => (
              <CNavItem data-tab={t.name} key={t.name} onClick={() => setActiveTab(t.name)}>
                <CNavLink data-tab={t.name}>{t.name}</CNavLink>
              </CNavItem>
            ))}
          </CNav>
          <CTabContent>
            {tabs.map((t) => (
              <CTabPane data-tab={t.name} key={t.name}>
                <div className="mt-3">
                  <t.component
                    data={data}
                    files={files}
                    cyberTiplineId={id}
                    report={report}
                    loadingApi={loadingApi}
                    afterSubmit={() => {
                      loadData();
                      setActiveTab(tabs[1].name);
                    }}
                  />
                </div>
              </CTabPane>
            ))}
          </CTabContent>
        </CTabs>
      </CCardBody>
      <CCardFooter>
        <button className="btn btn-secondary" onClick={() => window.history.back()}>
          Kembali
        </button>
        {activeTab === "Detail" && (
          <>
            <button className="btn btn-success mx-1" onClick={() => props.history.push(`/cyber-tipline/edit?id=${id}`)}>
              Edit
            </button>
            {loadingPdf ? (
              <LoadingButton color="danger" />
            ) : (
              <button className="btn btn-danger" onClick={downloadPdf}>
                Download PDF
              </button>
            )}
          </>
        )}
      </CCardFooter>
    </CCard>
  );
};

export default CyberTiplineDetail;
