import LoadingState from "../../../../components/LoadingState";

const TabDetail = ({ data, loadingApi }) => {
  if (loadingApi) return <LoadingState />;
  return (
    <div className="row">
      <div className="col-md-6">
        <table className="table">
          <tbody>
            {data[0].map((d, i) => (
              <tr key={i}>
                <th width="200">{d.label}</th>
                <td width="1">:</td>
                <td className="pl-0">{d.value}</td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
      <div className="col-md-6">
        <table className="table">
          <tbody>
            {data[1].map((d, j) => (
              <tr key={j}>
                <th width="200">{d.label}</th>
                <td width="1">:</td>
                <td className="pl-0">{d.value}</td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
    </div>
  );
};

export default TabDetail;
