import { CModal, CModalBody, CModalFooter, CModalHeader, CModalTitle } from "@coreui/react";
import { useRef, useState } from "react";
import { FaTrash } from "react-icons/fa";
import { uploadFile, deleteFile } from "../../../../api";
import ConfirmDialog from "../../../../components/ConfirmDialog";
import LoadingButton from "../../../../components/LoadingButton";
import LoadingState from "../../../../components/LoadingState";
import constant from "../../../../constants";

const TabFile = ({ files, cyberTiplineId, afterSubmit, loadingApi }) => {
  // const files = files?.fileInfos;
  const [fileName, setFileName] = useState("");
  const [fileUrl, setFileUrl] = useState("");
  const [toUpload, setToUpload] = useState("");
  const [modal, setModal] = useState(null);
  const [type, setType] = useState("");
  const [toDelete, setToDelete] = useState("");
  const [confirmDialog, setConfirmDialog] = useState(false);
  const [loadingDelete, setLoadingDelete] = useState(false);
  const [loadingUpload, setLoadingUpload] = useState(false);
  const fileRef = useRef(null);

  const toggle = () => {
    setModal(null);
    setFileName("");
    setFileUrl("");
    if (fileRef !== null) fileRef.current.value = null;
  };

  const handleClickFile = (f) => {
    setFileName(f.originalName);
    setFileUrl(f.url);
    setModal("iframe");
    const split = f.originalName.split(".");
    const x = split[split.length - 1];
    const images = ["jpg", "png", "jpeg"];
    if (images.includes(x)) setType("image");
    else setType("notImage");
  };

  const handleClickTrash = (f) => {
    setToDelete(f.id);
    setConfirmDialog(true);
    setFileName(f.originalName);
  };

  const onNegative = () => {
    setToDelete("");
    setConfirmDialog(false);
    setFileName("");
  };

  const uploadFiles = async () => {
    setLoadingUpload(true);
    try {
      await uploadFile(toUpload, cyberTiplineId);
      setModal(null);
      await afterSubmit();
    } catch (err) {}
    setLoadingUpload(false);
  };

  const onPositive = async () => {
    setLoadingDelete(true);
    try {
      await deleteFile(toDelete);
      setConfirmDialog(false);
      await afterSubmit();
    } catch (err) {}
    setLoadingDelete(false);
  };

  if (loadingApi) return <LoadingState />;
  return (
    <>
      <div className="row">
        <div className="col-md-6">
          {files.length === 0 && <h5>Tidak ada file.</h5>}
          <button className="btn btn-primary btn-sm" onClick={() => setModal("upload")}>
            Tambah File Baru
          </button>
          <ul className="mt-3">
            {files.map((file) => (
              <li key={file.id} className="text-primary mb-3">
                <h5 className="d-flex align-items-center cursor-pointer">
                  <span onClick={() => handleClickFile(file)} className="mr-2">
                    {file.originalName}
                  </span>
                  <FaTrash
                    style={{ color: "#d9534f", fontSize: 16 }}
                    className="cursor-pointer"
                    onClick={() => handleClickTrash(file)}
                  />
                </h5>
              </li>
            ))}
          </ul>
        </div>
      </div>
      <CModal show={modal === "iframe"} onClose={toggle} size="xl">
        <CModalHeader>
          <CModalTitle>{fileName}</CModalTitle>
        </CModalHeader>
        <CModalBody>
          {type === "image" ? (
            <img src={`${constant.FILE_URL}/${fileUrl}`} width={"100%"} alt={fileName} />
          ) : (
            <div className="embed-responsive embed-responsive-16by9" style={{ height: "80vh" }}>
              <iframe
                className="embed-responsive-item"
                title={fileName}
                src={`${constant.FILE_URL}/${fileUrl}`}
                allowFullScreen={true}
              ></iframe>
            </div>
          )}
        </CModalBody>
      </CModal>
      <CModal show={modal === "upload"} onClose={toggle}>
        <CModalHeader>
          <CModalTitle>Upload File</CModalTitle>
        </CModalHeader>
        <CModalBody>
          <input type="file" ref={fileRef} onChange={(e) => setToUpload(e.target.files[0])} className="form-control-file" />
        </CModalBody>
        <CModalFooter>
          <button className="btn btn-secondary" onClick={toggle}>
            Cancel
          </button>
          {loadingUpload ? (
            <LoadingButton />
          ) : (
            <button className="btn btn-primary" onClick={uploadFiles}>
              Upload
            </button>
          )}
        </CModalFooter>
      </CModal>
      <ConfirmDialog
        show={confirmDialog}
        text={
          <span>
            Apakah anda yakin akan menghapus file <b>{fileName}</b>
          </span>
        }
        onNegative={onNegative}
        btnLoading={loadingDelete}
        onPositive={onPositive}
      />
    </>
  );
};

export default TabFile;
